@import "../../styles/helpers/_helpers";

.arrow-next,
.arrow-prev {
	width: 40px;
	height: 40px;
	background-color: #c5c8cd;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 1;
	cursor: pointer;
	transition: all 0.2s ease-in-out;
	display: flex;
	justify-content: center;
	align-items: center;
	&:hover {
		background-color: #3c4658;
	}
	&:before {
		content: "";
		display: block;
	}
}
.arrow-prev {
	left: 0;
	&:before {
		content: "";
		@include sprite($arrow-left);
	}
}
.arrow-next {
	right: 0;
	&:before {
		content: "";
		@include sprite($arrow-right);
	}
}